.gpt3__features{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gpt3__features-heading{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}

.gpt3__features-heading h1{
    font-family: var(font-family);
    font-weight: 808;
    font-size: 34px;
    line-height: 45px;
}

.gpt3__features-heading p{
    font-family: var(font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #ff8a71;
    margin-top: 2rem;
    cursor: pointer;
}

.gpt3__features-container{
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width:990px) {
    .gpt3__features{
        flex-direction: column;
    }
    
    .gpt3__features-heading{
        margin: 0 0 2rem 0;
    }
}

@media screen and (max-width:550px) {
    .gpt3__features-heading {
        font-size: 28px;
        line-height: 38px;
    }

}

@media screen and (max-width:350px) {
    .gpt3__features-heading {
        font-size: 18px;
        line-height: 20px;
    }

}